              transform: translateY(0px);
  }
  to {
      -webkit-transform: translateY(-2000px);
              transform: translateY(-2000px);
  }
}


@keyframes animStar {
  from {
      -webkit-transform: translateY(0px);
              transform: translateY(0px);
  }
  to {
      -webkit-transform: translateY(-2000px);
              transform: translateY(-2000px);
  }
}
.btnFlex {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 30vh;
  /* justify-content: center; */
}
