  .btnFlex{
    display: none;
  }
}
.btn {
  color: #fff;
  cursor: pointer;
  font-size: 18px;
  font-weight: 800;
  line-height: 10px;
  position: relative;
  text-decoration: none;
  text-transform: uppercase;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
@media (min-width: 600px) {
  .btn {
    margin: 0 3em 1.2em;
  }
}
.btn:hover {
  text-decoration: none;
  color: #fff;
  border: 0px;
}

.btn-2 {
  position: relative;
  overflow: hidden;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border: 0px;
  /* z-index: 6; */
}
button .btn-2:focus {
  outline: none !important;
}
.btn-2:after {
  position: absolute;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  content: "";
  width: 0;
  left: 50%;
  bottom: 0;
  height: 2px;
  background: #f7f7f7;
  /* border: 0px; */
}
.btn-2:hover {
  cursor: pointer;
}
.btn-2:hover:after {
  width: 100%;
  left: 0;
}
.btn-2:focus {
  box-shadow: none;
}
.active-bottom {
  border-bottom: 2px solid white;
}

